.logo__img {
  width: 66px;
  height: 52px;
}

.footer {
  padding-top: 60px;
  padding-bottom: 60px;
  background-color: $background-color;
  box-shadow: 0px -4px 24px rgba(105, 105, 105, 0.1);
  background-image: url(../images/footer/bg-1_mob@1x.png),
    url(../images/footer/bg-2_mob@1x.png), url(../images/footer/bg-3_mob@1x.png);
  background-repeat: no-repeat;
  background-position: top 132px left, top right, bottom center;
  background-size: 110px 116px, 104px 259px, 253px 66px;

  @include retina {
    background-image: url(../images/footer/bg-1_mob@2x.png),
      url(../images/footer/bg-2_mob@2x.png),
      url(../images/footer/bg-3_mob@2x.png);
  }

  @include tablet {
    padding-top: 100px;
    padding-bottom: 100px;
    background-image: url(../images/footer/bg-1_tab@1x.png),
      url(../images/footer/bg-2_tab@1x.png);
    background-position: top right, bottom center;
    background-size: 203px 365px, 785px 141px;

    @include retina {
      background-image: url(../images/footer/bg-1_tab@2x.png),
        url(../images/footer/bg-2_tab@2x.png);
    }
  }

  @include desktop {
    background-image: url(../images/footer/bg-1_desk@1x.png);
    background-position: bottom center;
    background-size: 1211px 266px;

    @include retina {
      background-image: url(../images/footer/bg-1_desk@2x.png);
    }
  }
}

// footer top part
.footer__top {
  @include desktop {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    margin-bottom: 80px;
  }
}

.footer__navigation {
  display: flex;
  margin-bottom: 40px;

  @include desktop {
    margin-bottom: 0;
  }
}

.footer__logo {
  margin-right: 81px;
  transition: transform 300ms ease;

  @include tablet {
    margin-right: 129px;
    &:hover,
    &:focus {
      transform: scale(1.5);
    }
  }

  @include desktop {
    margin-right: 148px;
  }
}

.navigation__list--footer {
  @include tablet {
    width: 320px;
  }

  @include desktop {
    width: 320px;
  }
}

.navigation__item--footer {
  transition: background-color 300ms ease;
}

.navigation__item--footer:hover,
.navigation__item--footer:focus {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 4px 12px;
  background-color: $yellow-main;
  border-radius: 25px;
}

.footer__subscribe {
  margin-bottom: 60px;

  @include desktop {
    margin-bottom: 0;
  }
}

.footer__form-subscribe {
  @include tablet {
    column-gap: 0px;
  }

  @include desktop {
    flex-wrap: nowrap;
  }
}

.footer__subscribe-title {
  font-weight: 500;
  font-size: 14px;
  line-height: 1.5;
  color: $primary-text-color;
  margin-bottom: 12px;
}

.footer__form-subscribe {
  margin-bottom: 20px;

  @include tablet {
    display: flex;
    margin-bottom: 0;
  }

  @include desktop {
  }
}

.footer__label {
  margin-bottom: 5px;

  @include tablet {
    margin-bottom: 0;
  }

  @include desktop {
  }
}

.footer__button {
  display: block;
  margin-left: auto;

  @include tablet {
    margin-left: 13px;
  }
}

.footer__subscribe-sharing {
  font-size: 10px;
  line-height: 1.2;
}

.footer__subscribe-link {
  text-decoration-line: underline;
  color: $dark-green;
}

// footer bootom part
.footer__bottom {
  @include tablet {
    display: flex;
    justify-content: space-between;
  }
}

.footer__agreement {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 32px;

  @include mobile-only {
    margin-bottom: 40px;
  }

  @include tablet {
    gap: 20px;
  }
}

.footer__agreement-link {
  font-size: 10px;
  line-height: 1.2;
  text-decoration: underline;
  color: $dark-green;

  @include tablet {
    font-size: 12px;
    line-height: 1.16;
  }
}

.footer__meta {
  text-align: center;
  font-size: 10px;
  line-height: 1.2;

  @include tablet {
    font-size: 12px;
    line-height: 1.16;
  }
}
.form-subscribe__label {
  display: block;
  width: 100%;
  min-width: 280px;
  max-width: 440px;
  @include tablet{
    max-width: 394px;
  }
}

.form-subscribe__input {
  width: 100%;
  height: 42px;
  padding: 12px;
  border: 1px solid $yellow-main;
  border-radius: 30px;
  background-color: transparent;
  outline: transparent;
  transition: border 300ms ease;

  &:not(:placeholder-shown):required:invalid {
    border: 1px solid $warning-text-color;
  }

  &:not(:placeholder-shown):required:valid {
    border: 1px solid $dark-green;
  }
}

.form-subscribe__warning {
  display: flex;
  gap: 4px;
  align-items: center;
  font-size: 10px;
  line-height: 1.5;
  color: $warning-text-color;
  opacity: 0;
  transition: opacity 300ms ease;

  .form-subscribe__input:not(:placeholder-shown):required:invalid + & {
    opacity: 1;
  }
}

.form-subscribe__warning-icon {
  fill: none;
  stroke: $warning-text-color;
}

.form-subscribe__button {
  min-width: 103px;
  height: 42px;
  font-weight: 500;
  font-size: 13px;
  line-height: 1.5;
  color: $primary-text-color;
  background: $background-btn;
  border: 1px solid $dark-green;
  border-radius: 30px;
  transition: background-color 300ms ease;
  @include tablet {
    margin-left: 13px;
  }

  &:hover,
  &:focus {
    background-color: $yellow-main;
  }
  &--accent {
    background-color: $button-green;
    border-color: transparent;

    &:hover,
    &:focus {
      background-color: $button-green;
    }
  }
}